import React, { Component } from "react";
import Img1 from '../src/images/julia.jpeg'
import Img2 from '../src/images/gordon.jpg';
import Img3 from '../src/images/brad.jpeg'
import Img4 from '../src/images/tom.jpg'
import Img5 from '../src/images/leo.jpg';


class Galerie extends Component {
 render() {
 return (
 <div>
 <h2>Some Photos</h2>
 <p>
     <img src={Img1} width="180px" height="150px" id="images" />
     <img src={Img2} width="180px" height="150px" id="images" />
     <img src={Img3} width="180px" height="150px" id="images" />
     <img src={Img4} width="180px" height="150px" id="images" />
     <img src={Img5} width="180px" height="150px" id="images" />


 </p>
 </div>
 );
 }
}
export default Galerie;